:root {
    --dark-background-color: #2C3333;
    --dark-text-color: #E7F6F2;
    --dark-text-color-2: #A5C9CA;
    --dark-h1-color: white;
    --dark-blockquote-background-color: #395B64;
    --dark-blockquote-text-color: #A5C9CA;
    --dark-code-background-color: #A5C9CA;
    --dark-code-text-color: #2C3333;
}


body.theme-dark{
    background-color: #2C3333;
    background-color: var(--dark-background-color);
    color: #E7F6F2;
    color: var(--dark-text-color);
}

.theme-dark .home-page-title{
    color: #A5C9CA !important;
    color: var(--dark-text-color-2) !important;
}

.theme-dark h1 {
    color: white;
    color: var(--dark-h1-color);
}

.theme-dark a {
    color: #A5C9CA;
    color: var(--dark-text-color-2);
}

.theme-dark small{
    color: white;
}

.theme-dark #MD blockquote{
    background-color: #395B64 !important;
    background-color: var(--dark-blockquote-background-color) !important;
    color: #A5C9CA !important;
    color: var(--dark-blockquote-text-color) !important;   
}

.theme-dark #MD code {
    background-color: #A5C9CA !important;
    background-color: var(--dark-code-background-color) !important;
    color: #2C3333;
    color: var(--dark-code-text-color);
}

.theme-dark #MD pre code{
    /* fix color */
    background-color: rgb(45, 45, 45) !important; 
}

.theme-dark input{
    background-color: #2C3333;
}

.theme-dark #Search-Bar button[type=submit]{
    background-color: #E7F6F2;
    background-color: var(--dark-text-color);
}
html, body, #root {
  margin: 0;
  font-family: "Iansui094-Regular";
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  padding: 0% 0% 0% 0%;
  height: 100%;
  box-sizing: border-box;
}

.text-center {
  text-align: center;
}

.app-container {
  padding: 0%;
  border: 0px solid #b7b7b7;
  border-radius: 5px;
  box-sizing: border-box;
  /* max-width: 500px; */
  max-height: 100%;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
}

.render-container{
  margin: 0 auto;
  max-width: 800px;
  padding-bottom: 200px;
}

/* width */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

#App .context {
    max-width: 800px;
    min-width: 600px;
    margin: 0 auto;
    position: relative;
}

#App h1 {
    font-size: 32px;
}

#App .home-page-title{
    color: black;
    text-decoration: none;
}

@media screen and (max-width: 768px) {
    #App .context {
        width: 100%;
        margin: 0 auto;
        position: relative;

    }
}

@media screen and (max-width: 576px) {
    #App .context {
        margin-left: 0px;
        box-sizing: border-box;
        max-width: 576px;
        min-width: 380px;
        position: relative;
    }
}
#Post-Block{
    padding: 15px;
    margin-bottom: 50px;
    border: #d8d8d8 solid 1px;
    border-radius: 5px;
}

#Post-Block hr{
    border: 1px solid #d8d8d8;
}

#Post-Block .title-badge{
    width: 100px;
    margin-left: 10px;
    position: relative;
    top: 4px;
}

#Post-Block a.post-title{
    font-size: 18px;
}

@media screen and (max-width: 768px) {
    #Post-Block{
        padding: 12px;
        max-width: 100%;     
    }
}

@media screen and (max-width: 576px) {
    #Post-Block{
        width: 100%;
        padding: 12px;
        box-sizing: border-box;
        margin: 0 auto;
        margin-bottom: 20px;
    }
}
#Search-Bar {
    margin-bottom: 32px;
}

#Search-Bar input[type=text] {
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    height: 24px;
    padding-left: 5px;
    margin-right: 5px;
}

#Search-Bar input[type=text]:focus{
    outline-width: 0px;
}

#Search-Bar button[type=submit]{
    height: 28px;
    background-color: #e3e3e3;
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 0px 0px;
}
#MD-Preview{
    overflow: hidden;
}
#MD-Preview document-info,
#MD-Preview .md-preview h1{
    visibility: hidden;
    line-height: 0px;
}

#Loading{
    position: absolute;
    top: 10px;
    left: 10px;
}
.drkmd-toggle-button{
    visibility: hidden !important;
}

#Corner-Menu {
    color: gray;
    font-size: 3px;
    position: fixed;
    bottom: 8%;
    right: 2.5%;
}

#Corner-Menu .link {
    height: 42px;
    width: 42px;
    border-radius: 50%;
    display: block;
    margin-top: 15px;
}

#Corner-Menu .link svg,
#Corner-Menu .link a {
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
}

#Corner-Menu .link svg:hover,
#Corner-Menu .link a:hover{
    color: gray;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    #Corner-Menu {
        color: gray;
        font-size: 3px;
        position: fixed;
        bottom: 2%;
        right: 6%;
    }

    #Corner-Menu .link {
        height: 36px;
        width: 36px;
        border-radius: 50%;
        display: block;
        margin-top: 15px;
    }
}
#Footer{
    text-align: center;
    color: gray;
    font-size: 10pt;
    position: fixed;
    bottom: 2.5%;
    right: 2.5%;
}

#Footer a{
    color: #6699FF;
}

#Footer a:link{
    text-decoration: none;
}

@media screen and (max-width: 768px){
    #Footer{
        color: gray;
        font-size: 10pt;
        position: initial;
    }
}
.badge{
    margin-right: 5px;
}

#MD .footer{
    color: gray;
    margin-top: 60px;
}

#MD .footer a{
    color: #6699FF;
}

#MD h2{
    text-decoration: underline;
    -webkit-text-decoration-color: #3498DB;
            text-decoration-color: #3498DB;
    line-height: 60px;
}

#MD pre{
    max-width: 100%;
    overflow: auto;
}

#MD code{
    background-color: #FCF3CF;
    padding: 0px 5px 0px 5px;
    box-sizing: border-box;
    border-radius: 2px;
    font-family: monospace;
    font-size: larger;
}

#MD img{
    max-width: 100%;
}

#MD blockquote{
    color: #626567;
    background-color: #F2F3F4;
    padding: 1px 12px 1px 12px;
    font-size: smaller;
}

#MD document-info{
    display: block;
    font-family: monospace;
    white-space: pre;
    margin: 1em 0;
    overflow: hidden;
}
